@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Merriweather:wght@400;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  min-width: 320px;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  font-family: "Inter", sans-serif;
  font-size: 2rem;
  line-height: 2.4rem;
  font-weight: 400;
  color: #fff;
  background: #000;
}

img {
  max-width: 100%;
}

a {
  color: #fff;
  transition: color 0.3s;
}
a:hover {
  color: #bdae82;
}

.container {
  max-width: 140rem;
  padding: 0 2rem;
  margin: 0 auto;
}

.btn {
  display: inline-block;
  background: #bdae82;
  line-height: 4rem;
  color: #000;
  text-align: center;
  transition: color 0.3s;
}
.btn:hover {
  color: #fff;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Merriweather", serif;
  font-size: 4rem;
  line-height: 8rem;
  font-weight: bold;
  color: #bdae82;
  margin: 10rem auto 6rem;
  text-align: center;
  max-width: 70rem;
  z-index: 1;
}
.title::before, .title::after {
  content: "";
  display: block;
  flex: 1 1 auto;
  height: 0.2rem;
  background: #bdae82;
}
.title::before {
  margin-right: 2rem;
}
.title::after {
  margin-left: 2rem;
}

.header {
  position: relative;
}
.header__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
}
.header__lang {
  margin-left: 3rem;
}
.header__lang span {
  color: #bdae82;
}
.header__lang-menu {
  display: none;
  margin-left: 3rem;
}
.header__lang-menu span {
  color: #bdae82;
}

.menu {
  margin-left: auto;
}
.menu__btn {
  display: none;
  position: relative;
  width: 3.5rem;
  height: 2.4rem;
}
.menu__list {
  display: flex;
}
.menu__item {
  margin-left: 3rem;
}

.top {
  background: url(../../assets/img/bg-top.jpg) no-repeat top center;
  background-size: cover;
  min-height: 91rem;
  padding-top: 9rem;
}
.top__items {
  padding: 16rem 0 4rem 6rem;
  max-width: 47rem;
}
.top__title {
  font-family: "Merriweather", serif;
  font-size: 6rem;
  line-height: 12rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 4.5rem;
}
.top__text {
  font-size: 2rem;
  line-height: 4rem;
  font-weight: normal;
  color: #fff;
  margin-bottom: 2.5rem;
}
.top__link {
  padding: 0.8rem 7rem;
}

.skills__items {
  display: flex;
  justify-content: space-between;
  text-align: center;
}
.skills__item {
  max-width: 32rem;
  margin-right: 4rem;
}
.skills__item:last-child {
  margin-right: 0;
}
.skills__img {
  margin-bottom: 5rem;
}
.skills__item-title {
  font-weight: bold;
  line-height: 4rem;
  margin-bottom: 2rem;
}
.skills__text {
  font-size: 1.8rem;
  line-height: 3.6rem;
}

.portfolio__links {
  text-align: center;
  margin-bottom: 6rem;
}
.portfolio__link {
  display: inline-block;
  background: transparent;
  border: 1px solid #bdae82;
  padding: 0.8rem 0;
  width: 22rem;
  color: #bdae82;
  transition: all 0.3s;
}
.portfolio__link:hover {
  background: #bdae82;
  color: #fff;
}
.portfolio__link:active {
  background: #bdae82;
  color: #000;
}
.portfolio__items {
  margin: 0 auto;
  max-width: 140rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
}
.portfolio__item {
  display: flex;
}
.portfolio__item-image {
  max-width: 100%;
}

.price {
  margin-bottom: 10rem;
}
.price__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
}
.price__item {
  display: flex;
  flex-direction: column;
  padding: 4.3rem 0;
  align-items: center;
  width: 45rem;
  border: 3px solid #bdae82;
}
.price__item-title {
  font-size: 2.8rem;
  line-height: 5.6rem;
  font-weight: bold;
  color: #fff;
  font-family: "Merriweather", serif;
  margin-bottom: 2rem;
}
.price__item-link {
  width: 22rem;
  padding: 0.8rem 0;
}
.price__cost {
  margin-bottom: 2rem;
  font-size: 2.4rem;
  line-height: 4.8rem;
  font-weight: bold;
  color: #bdae82;
  font-family: "Merriweather", serif;
}
.price__benefits {
  display: flex;
  flex-direction: column;
  margin-bottom: 4rem;
}
.price__benefits-item {
  font-size: 1.8rem;
  line-height: 3.6rem;
}

.contacts {
  padding-top: 6rem;
  background: url(../../assets/img/contacts.jpg) no-repeat top center;
  background-size: cover;
  height: 100%;
}
.contacts__inner {
  display: flex;
  justify-content: flex-end;
}
.contacts__title {
  font-size: 4rem;
  line-height: 8rem;
  font-weight: bold;
  color: #bdae82;
  font-family: "Merriweather", serif;
  margin-bottom: 6rem;
}
.contacts__form {
  display: flex;
  flex-direction: column;
  max-width: 45rem;
}
.contacts__item {
  background: transparent;
  border: 0.2rem solid #bdae82;
  margin-bottom: 2rem;
  padding: 2rem 0 2rem 2rem;
  width: 45rem;
  color: #bdae82;
  font-size: 20px;
  line-height: 40px;
}
.contacts__item::placeholder {
  font-size: 20px;
  line-height: 40px;
  color: #bdae82;
}
.contacts__item-link {
  width: 22rem;
  line-height: 40px;
  margin-top: 3rem;
  padding: 0.8rem 0;
}

.footer__items {
  min-height: 15rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__item:first-child {
  display: flex;
}
.footer__text {
  font-size: 20px;
  line-height: 50px;
}
.footer__text:first-child {
  margin-right: 2rem;
}
.footer__link {
  margin-right: 2rem;
}
@media (max-width: 768px) {
  .lock {
    overflow: hidden;
  }

  .header__lang {
    display: none;
  }
  .header__lang-menu {
    display: block;
  }

  .menu {
    margin-left: auto;
    width: 100%;
  }
  .menu__btn {
    display: block;
    background: transparent;
    position: fixed;
    top: 4rem;
    right: 2rem;
    cursor: pointer;
    transform: translateY(-50%);
    z-index: 2;
  }
  .menu__btn-line {
    display: block;
    width: 100%;
    height: 0.2rem;
    background: white;
    position: absolute;
    left: 0;
    transition: all 0.3s linear;
  }
  .menu__btn-line--first {
    top: 0;
  }
  .menu__btn-line--second, .menu__btn-line--third {
    top: 35%;
  }
  .menu__btn-line--fourth {
    bottom: 50%;
    height: 0;
    color: white;
  }
  .menu__btn--active .menu__btn-line--first, .menu__btn--active .menu__btn-line--fourth {
    opacity: 0;
  }
  .menu__btn--active .menu__btn-line--second {
    transform: translateY(-50%) rotate(45deg);
  }
  .menu__btn--active .menu__btn-line--third {
    transform: translateY(-50%) rotate(-45deg);
  }
  .menu__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    position: fixed;
    padding: 1rem 0;
    text-align: center;
    top: 0;
    left: 0;
    right: 0;
    background-color: #000;
    opacity: 0.9;
    transform: translatex(80rem);
    transition: all 0.5s;
  }
  .menu__list--active {
    transform: translateY(0);
    z-index: 1;
  }
  .menu__list-link {
    display: block;
    padding: 2rem 0;
  }

  .header__lang-menu {
    position: absolute;
    right: 2rem;
    bottom: 1rem;
    margin-left: 0;
  }

  .skills__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .skills__item {
    margin: 0 auto;
  }
  .skills__item:last-child {
    margin: 0 auto;
  }

  .portfolio__links {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    justify-items: center;
  }

  .price {
    margin-bottom: 10rem;
  }
  .price__items {
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
  }
  .contacts__inner {
    justify-content: center;
  }
}
@media (max-width: 576px) {
  .portfolio__items {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    gap: 2rem;
  }
}
@media (max-width: 480px) {
  .top__items {
    display: flex;
    flex-direction: column;
    padding: 0 2rem;
    margin-bottom: 2rem;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .top__title {
    font-size: 4rem;
    line-height: 6rem;
    font-weight: bold;
    color: #fff;
  }
  .top__text {
    font-size: 1.8rem;
    line-height: 3rem;
  }

  .skills__items {
    grid-template-columns: repeat(1, 1fr);
  }
  .skills__item {
    margin-bottom: 3rem;
  }
  .skills__item-title {
    margin-bottom: 1rem;
  }
  .skills__img {
    margin-bottom: 1rem;
  }

  .portfolio__links {
    grid-template-columns: repeat(1, 1fr);
  }
  .portfolio__link {
    width: 100%;
  }
  .portfolio__items {
    grid-template-columns: repeat(1, 1fr);
  }

  .price__item {
    width: 100%;
  }

  .contacts__form {
    max-width: none;
  }
  .contacts__item {
    width: 100%;
  }
  .contacts__title {
    margin-bottom: 0;
  }
}