@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Merriweather:wght@400;700&display=swap');
$merri:'Merriweather', serif;
$inter: 'Inter', sans-serif;
$primaryFont: $inter;
$secondaryFont: $merri;
@mixin familyPrime {font-family: $primaryFont;}
@mixin familySecond {font-family: $secondaryFont;}
@mixin font($size, $lh, $weight, $color) {font-size: $size;line-height: $lh;font-weight: $weight;color: $color;}
@mixin font-lite($size, $lh) {font-size: $size;line-height: $lh;}
